.socials__list {
  display: grid;
  grid-template-columns: repeat(3, auto);
  gap: 27px;

  @media (max-width: $tablet) {
    gap: 22px;
  }

  @media (max-width: $tel) {
    gap: 18px;
  }
}

.socials__link {
  display: block;
  padding: 10px 0;

  &:focus-visible {
    outline: 1px solid $color-background;
    outline-offset: 2px;
  }

  &:hover {
    opacity: 0.8;
  }

  &:active {
    opacity: 0.5;
  }

  @media (max-width: $tel) {
    padding: 5px 0;
  }
}


.socials__link > svg {
  @media (max-width: $tel) {
    width: 17px;
    height: 16px;
  }
}
